/*
* Element: Input fields
*/
/* #input
    Common input field. Shared size and type resets for all input fields of the following:
    select,
    textarea,
    input of the type:
    - text
    - password
    - datetime
    - datetime-local
    - date
    - month
    - time
    - week
    - number
    - email
    - url
    - search
    - tel
    - color
********************/
.input {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 100px;
  vertical-align: bottom;
}
.input:focus {
  outline: 0;
}
.input::-ms-expand {
  border: 0;
  background-color: transparent;
}
.input[disabled],
.input[readonly],
fieldset[disabled] .input {
  background-color: #acacac;
  opacity: 1;
}
.input[disabled],
fieldset[disabled] .input {
  cursor: disabled;
}
textarea .input {
  height: auto;
}
input[type='search'] {
  -webkit-appearance: none;
}
input[type='radio'],
input[type='checkbox'] {
  margin: 4px 0 0;
  margin-top: 1px \9;
  line-height: normal;
}
input[type='file'] {
  display: block;
}
input[type='range'] {
  display: block;
  width: 100%;
}
